import time from "../../common/time.js";
import tagSexAge from "../common/tag-sex-age.vue"
export default {
	components:{
		tagSexAge
	},
	props:{
		item:Object,
		userInfo:Object,
		detail:Object
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu,
			list: "list3"

		}
	},
	computed:{
		createDate(){
			let data = new Date(this.item.createTime).getTime();
			
			return time.gettime.sumAge(data)
		}
	},
	methods:{
		goToUserInfo(item){
			this.$emit("goToUserInfo",item)
			// uni.navigateTo({
			// 	url:'../../pages/user-space/user-space?uid='+this.item.id
			// })
		},
		guanzhu(){
				console.log(this.userInfo.id,this.detail.uid)
			if(this.isguanzhu){
				this.$http.delete('/user/active',{
					fromId:this.userInfo.id,
					toId:this.detail.uid
				})
				this.isguanzhu=false;
				uni.showToast({
					title: '取消关注',
				});
			}else{
				this.$http.post('/user/active',{
					fromId:this.userInfo.id,
					toId:this.detail.uid
				})
				this.isguanzhu=true;
				uni.showToast({
					title: '关注成功',
				});
			}
			
		},
		imgdetail(index){
			uni.previewImage({
				current:index,
				urls:this.item.images
			})
		}
	}
}
